.container{
    height: 100%;
    display: flex;
    justify-content: center;   
}
.loader{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies{
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70x;
}
.loader{
    color: #4d9df8;
    font-weight: bold;
    font-size: 25px;

}

.icon{
    color: #2c82e4;
}

@media screen and (max-width:1090px){
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
} 

@media screen and (max-width:600px){
    .movies{
        text-align: center;
        margin: auto;
    }
} 